<template>
	<Modal :settingInfo="settingInfo" @hideModal="hideModal">
		<template slot="modal-header">
			<h2 class="modal-title">{{ $t('common.user') }} {{ $t('head.info') }}</h2>
			<button type="button" class="btn-icon" aria-label="Close" @click="hideModal">
				<i class="icon-close"></i>
			</button>
		</template>
		<template slot="modal-body">
			<ul class="nav nav-tabs flex-shrink-0">
				<li class="nav-item" v-for="(tabObj, index) in tabList" :key="tabObj.id">
					<a
						href="javascript:void(0);"
						:class="{ 'nav-link': true, active: selectedIdx === index }"
						@click="changeTab(index)"
						v-text="tabObj.label"
					>
					</a>
				</li>
			</ul>
			<div class="panel-body tab-content" style="height: 43vh">
				<div :class="{ [modalMode]: true, 'tab-pane': true, active: selectedIdx === idx }" v-for="(detailInfo, idx) in tabList" :key="idx">
					<DetailBasic
						ref="component"
						:detailInfo="detailInfo"
						:detailData="userInfoData"
						:mode="modalMode"
						v-if="'basic' === detailInfo.type"
					/>
					<div class="form-table form-table-responsive" v-else-if="'checkboxProperty' === detailInfo.type">
						<div class="form-table-wrap userInfo">
							<div class="form-table-head">
								<label class="col-lg-1"></label>
								<label class="col-lg-1">{{ $t('common.privilege.type') }}</label>
								<label class="col-lg-8">{{ $t('common.privilege') }}</label>
							</div>
							<div class="form-table-body" v-for="(propertyRow, propertyIndex) in totalUserPrivileges" :key="propertyIndex">
								<div class="col-lg-2">
									<div style="width: 100%">
										<select
											v-model="propertyRow.privilegeName"
											class="form-control view-disabled"
											:val="(propertyRow.privilegeName = propertyRow.admin ? 'Admin' : propertyRow.member ? 'Member' : 'None')"
											:readonly="true"
										>
											<option value="None">{{ $t('common.user.privilege.none') }}</option>
											<option value="Admin">{{ $t('common.user.privilege.admin') }}</option>
											<option value="Member">{{ $t('common.user.privilege.member') }}</option>
										</select>
									</div>
								</div>
								<div class="col_lg_2">
									<div style="width: 100%">
										<inputText
											vModel="privilegeType"
											:classObject="{ 'form-control': true, 'view-disabled': true }"
											:val="
												propertyRow.privilegeType === isSystem
													? 'System'
													: propertyRow.privilegeType === isBusiness
													? 'Business'
													: $t('common.role')
											"
											:readonly="true"
										/>
									</div>
								</div>
								<div class="col-lg-8">
									<div style="width: 100%">
										<inputText
											vModel="privilege"
											:classObject="{ 'form-control': true, 'view-disabled': true }"
											:val="propertyRow.privilegeId"
											:readonly="true"
										/>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</template>
		<template slot="modal-footer">
			<button type="button" class="btn btn-primary" v-if="'detail' === modalMode" @click="() => (this.modalMode = 'modify')">
				<i class="icon-edit"></i>{{ $t('head.update') }}
			</button>
			<button type="button" class="btn" v-if="'detail' === modalMode" @click="hideModal">
				{{ $t('head.close') }}
			</button>
			<button type="button" class="btn" v-if="'detail' !== modalMode" @click="() => (this.modalMode = 'detail')">
				{{ $t('head.cancel') }}
			</button>
			<button type="button" class="btn btn-primary" v-if="'detail' !== modalMode" @click="updateUserInfo">
				{{ $t('head.save') }}
			</button>
		</template>
	</Modal>
</template>

<script>
import merge from 'lodash/merge';
import { HttpReq, changeDateFormat } from '@/utils/common.js';
import { mapState } from 'vuex';
import Modal from '@/components/Modal.vue';
import DetailBasic from '@/components/detail/DetailBasic';
import inputText from '@/components/element/inputText';
import cloneDeep from 'lodash/cloneDeep';
import { encryptPassword } from '@/utils/common';

export default {
	name: 'userInfoModal',
	components: {
		Modal,
		DetailBasic,
		inputText,
	},
	props: {
		openModal: Boolean,
	},
	computed: {
		...mapState(['userInfo']),
		settingInfo() {
			return {
				isHeader: true,
				showModal: this.showModal,
				backdropMode: this.backdropMode,
				modalWidth: '900px',
				classObject: {
					'modal-dialog': true,
					'modal-dialog-centered': true,
					'modal-dialog-scrollable': true,
				},
			};
		},
	},
	watch: {
		openModal() {
			this.showModal = this.openModal;
		},
		modalMode() {
			this.getData(() => {
				this.totalUserPrivileges = this.userInfoData.totalUserPrivileges;

				this.totalUserPrivileges.forEach(info => {
					info.privilegeName = info.admin ? 'Admin' : info.member ? 'Member' : 'None';
				});
			});
		},
	},
	mounted() {
		this.getData(() => {
			this.totalUserPrivileges = this.userInfoData.totalUserPrivileges;
		});
	},
	methods: {
		async getData(callBack) {
			const userInfoResult = await this.userInfoReq.read({ userId: this.userInfo.userId }, true);

			if (`ok` !== userInfoResult.data.result) {
				this.$alert({ type: 'warn', message: this.$t('head.error.load.data.warn') });
				return;
			}

			this.userInfoData = userInfoResult.data.object;

			if (callBack) callBack();
		},
		hideModal() {
			this.showModal = false;
			this.$emit('hideModal');
		},
		changeTab(idx) {
			this.selectedIdx = idx;
		},
		async updateUserInfo() {
			let saveUserInfo = cloneDeep(this.userInfoData);

			this.$refs.component.forEach(comp => {
				saveUserInfo = merge(saveUserInfo, comp.getData());
			});

			saveUserInfo.passwordOld = encryptPassword(saveUserInfo.passwordOld);
			saveUserInfo.passwordNew = encryptPassword(saveUserInfo.passwordNew);

			const result = await this.userInfoReq.update(saveUserInfo);

			if ('ok' === result.data.result) {
				this.modalMode = 'detail';
				this.userInfoData.passwordOld = ' ';
				this.userInfoData.passwordNew = ' ';
				this.$alert({ type: 'normal', message: this.$t('head.update.notice') });
				this.getData();
			} else {
				this.$alert({ type: 'warn', message: result.data.error[0].message });
			}
		},
	},
	data() {
		return {
			modalMode: 'detail',
			selectedIdx: 0,
			showModal: false,
			backdropMode: 'full',
			userInfoReq: new HttpReq('/common/user/edit.json'),
			userInfoData: {},
			totalUserPrivileges: [],
			isSystem: 'S',
			isBusiness: 'b',
			tabList: [
				{
					type: 'basic',
					label: this.$t('head.basic.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'userId',
									label: this.$t('common.user.id'),
									isPK: true,
								},
								{
									type: 'text',
									vModel: 'userName',
									label: this.$t('common.user.name'),
									regExpType: 'name',
								},
								{
									type: 'text',
									vModel: 'department',
									label: this.$t('common.user.department'),
								},
								{
									type: 'text',
									vModel: 'telephone',
									label: this.$t('common.user.telephone'),
									regExpType: 'num',
								},
							],
							[
								{
									type: 'text',
									vModel: 'mobilephone',
									label: this.$t('common.user.mobilephone'),
									regExpType: 'num',
								},
								{
									type: 'text',
									vModel: 'email',
									label: this.$t('common.user.email'),
								},
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('head.update.userId'),
									readonly: true,
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('head.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value),
								},
							],
						],
					],
				},
				{
					id: 'Authentication',
					type: 'basic',
					label: this.$t('common.user.authentication.info'),
					content: [
						[
							[
								{
									type: 'select',
									vModel: 'userDisableYn',
									label: this.$t('common.user.disable'),
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Yn',
											orderByKey: true,
										},
										optionListName: 'openWindowList',
										optionFor: 'option in openWindowList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue',
									},
									readonly: true,
								},
								{
									type: 'text',
									vModel: 'loginIp',
									label: this.$t('common.user.loginIp'),
									readonly: true,
								},
								{
									type: 'password',
									vModel: 'passwordOld',
									label: this.$t('common.user.oldPassword'),
									regExpType: 'password',
								},
								{
									type: 'password',
									vModel: 'passwordNew',
									label: this.$t('common.user.newPassword'),
									regExpType: 'password',
								},
							],
							[
								{
									type: 'text',
									vModel: 'passwordExpiration',
									label: this.$t('common.user.passwordExpiration'),
									readonly: true,
									formatter: value => changeDateFormat(value),
								},
								{
									type: 'text',
									vModel: 'userExpiration',
									label: this.$t('common.user.userExpiration'),
									readonly: true,
									formatter: value => changeDateFormat(value),
								},
								{
									type: 'text',
									vModel: 'loginAttempts',
									label: this.$t('common.user.loginAttempts'),
									readonly: true,
								},
								{
									type: 'text',
									vModel: 'loginTimestamp',
									label: this.$t('common.user.loginTimestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value),
								},
							],
						],
					],
				},
				{
					type: 'checkboxProperty',
					label: `${this.$t('common.user')} ${this.$t('common.privilege')} ${this.$t('head.info')}`,
				},
			],
		};
	},
};
</script>
