<template>
	<div>
		<DashboardConfigStructure
			ref="dashboardConfigStructure"
			mod="add"
			:containerInfoOrg="containerInfo"
			:dashConstInfo="dashConstInfo"
			@changeContainer="changeContainer"
		>
			<template slot="dashboardRbtnConfigArea">
				<a href="javascript:void(0);" class="btn btn-primary" @click="saveContainer"> {{ $t('head.save') }} </a>
			</template>
		</DashboardConfigStructure>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import cloneDeep from 'lodash/cloneDeep';
import DashboardConfigStructure from '@/views/main/dashboard/DashboardConfigStructure.vue';
import { HttpReq } from '@/utils/common.js';

export default {
	name: `DashboardAdd`,
	props: {
		containerInfo: Object,
		dashConstInfo: Object,
	},
	components: {
		DashboardConfigStructure,
	},
	methods: {
		async saveContainer() {
			if (0 === this.$refs.dashboardConfigStructure.componentList.length) {
				this.$alert({
					type: `alert`,
					message: this.$t(`dashboard.deploy.noComponent`),
				});

				return;
			}

			const containerInfo = merge(
				{
					...cloneDeep(this.containerInfo),
					monitorComponents: cloneDeep(this.$refs.dashboardConfigStructure.componentList).map(element => {
						element.containerId = this.containerInfo.containerId;
						return element;
					}),
				},
				cloneDeep(this.$refs.dashboardConfigStructure.containerInfo),
			);

			let checkNormal = true;
			let warnAlertMsg = null;

			for (let i = 0; i < containerInfo.monitorComponents.length; i++) {
				const component = containerInfo.monitorComponents[i];

				if (`XVIEW` !== component.chartType && `DATATABLE` !== component.chartType) continue;

				if (`XVIEW` === component.chartType) {
					const xViewYAxisMax = component.xViewYAxisMax;
					const xViewTrans = component.xViewTrans;
					const xViewMinData = component.xViewMinData;

					if (!$.isNumeric(xViewYAxisMax) || !$.isNumeric(xViewTrans) || !$.isNumeric(xViewMinData)) {
						warnAlertMsg = this.$t(`dashboard.enter.xview.numericalCheck`);
						checkNormal = false;
						break;
					}

					if (xViewTrans < 0.1 || xViewTrans > 1.0) {
						warnAlertMsg = this.$t(`dashboard.enter.xview.rangeCheck`);
						checkNormal = false;
						break;
					}
				}

				if (`DATATABLE` === component.chartType) {
					const datatableRowCnt = component.datatableRowCnt;

					if (!$.isNumeric(datatableRowCnt)) {
						warnAlertMsg = this.$t(`dashboard.enter.number`);
						checkNormal = false;
						break;
					}
				}

				if (`INSTANCE` === component.chartType) {
					const instanceSummaryColCnt = component.instanceSummaryColCnt;

					if (!$.isNumeric(instanceSummaryColCnt)) {
						warnAlertMsg = this.$t(`dashboard.enter.number`);
						checkNormal = false;
						break;
					}
				}
			}

			if (!checkNormal) {
				this.$alert({ type: `warn`, message: warnAlertMsg });
				return false;
			}

			const httpReq = new HttpReq(`/igate/monitoring/dashboard/container.json`);

			const result = await httpReq.update(containerInfo);

			if (`ok` !== result.data.result) return;

			containerInfo.monitorComponents.forEach(component => {
				if (`XVIEW` === component.chartType) {
					localStorage.setItem(`xViewYAxisMax_${component.componentId}`, component.xViewYAxisMax);
					localStorage.setItem(`xViewTrans_${component.componentId}`, component.xViewTrans);
					localStorage.setItem(`xViewMinData_${component.componentId}`, component.xViewMinData);
				}

				if (`DATATABLE` === component.chartType) {
					localStorage.setItem(`datatableRowCnt_${component.componentId}`, component.datatableRowCnt);
				}

				if (`INSTANCE` === component.chartType) {
					localStorage.setItem(`instanceSummaryColCnt_${component.componentId}`, component.instanceSummaryColCnt);
				}
			});

			localStorage.setItem(`selectedContainerId`, containerInfo.containerId);

			this.$alert({
				type: `normal`,
				message: this.$t(`dashboard.add.success`),
			});

			this.changeContainer();
		},
		changeContainer() {
			this.unloadContainer();

			this.$emit(`changeContainer`, `view`);
		},
		unloadContainer() {
			this.$refs.dashboardConfigStructure.unloadContainer();
		},
	},
};
</script>
