<template>
	<div id="panel" ref="panel" class="panel panel-bottom">
		<div class="panel-dialog">
			<div class="panel-content">
				<header id="panel-header" class="panel-header sub-bar">
					<a
						href="javascript:void(0);"
						class="btn btn-icon backBtn"
						:title="$t('head.goBackward')"
						@click="goPreviousPanel"
						v-if="'modify' === panelMode"
						><i class="icon-left"></i>
					</a>
					<h2 class="sub-bar-tit">{{ subBarTitle }}</h2>
					<div class="ml-auto">
						<button type="button" class="btn-icon" @click="expandCollapsePanel" :title="$t('head.expand')">
							<i class="icon-expand"></i>
						</button>
						<button type="button" class="btn-icon" @click="closePanel" :title="$t('head.close')"><i class="icon-close"></i></button>
					</div>
				</header>

				<ul class="nav nav-tabs flex-shrink-0">
					<li class="nav-item" v-for="(tabObj, index) in tabList" :key="tabObj.id">
						<a
							href="javascript:void(0);"
							:class="{ 'nav-link': true, active: selectedIdx === index }"
							@click="changeTab(index)"
							v-text="tabObj.label"
						>
						</a>
					</li>
					<li class="nav-item" v-if="toShowResult">
						<a
							href="javascript:void(0);"
							:class="{ 'nav-link': true, active: selectedIdx === tabList.length }"
							@click="changeTab(tabList.length)"
						>
							{{ $t('head.executionResult') }}
						</a>
					</li>
				</ul>
				<!-- tab-pane -->
				<div class="panel-body tab-content">
					<div
						:class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === idx }"
						v-for="(detailInfo, idx) in tabList"
						:key="idx"
					>
						<DetailBasic
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-if="'basic' === detailInfo.type"
						/>
						<DetailProperty
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'property' === detailInfo.type"
						/>
						<DetailGrid
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'grid' === detailInfo.type"
						/>
						<DetailCustom
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'custom' === detailInfo.type"
						/>
						<component
							ref="component"
							:is="require(`@/views/detail${detailInfo.url}.vue`).default"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'bundle' === detailInfo.type"
						/>
					</div>
					<DetailResult
						:class="{ 'tab-pane': true, active: selectedIdx === tabList.length && showResultTab }"
						:httpReqResult="httpReqResult"
						v-if="toShowResult"
					/>
				</div>

				<footer id="panel-footer" class="panel-footer sub-bar">
					<div class="ml-auto" v-if="'detail' === panelMode || 'done' === panelMode">
						<a href="javascript:void(0);" class="btn" @click="guide" v-if="guideBtn && editor" :title="$t('igate.connector.guide')">
							{{ $t('igate.connector.guide') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="control(`load`)" v-if="loadBtn && editor" :title="$t('head.load')">
							{{ $t('head.load') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="control(`dump`)" v-if="dumpBtn && editor" :title="$t('head.dump')">
							{{ $t('head.dump') }}
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-primary"
							@click="excelExport"
							v-if="excelExportBtn && viewer"
							:title="$t('head.excel.output')"
						>
							<i class="icon-result"></i><span class="hide">{{ $t('head.excel.output') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-m"
							@click="control(`execute`)"
							v-if="executeBtn && editor"
							:title="$t('head.execute')"
						>
							<i class="icon-play"></i><span class="hide">{{ $t('head.execute') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-m"
							@click="control(`start`)"
							v-if="startBtn && editor"
							:title="$t('head.execute')"
						>
							<i class="icon-play"></i><span class="hide">{{ $t('head.execute') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn btn-m" @click="control(`stop`)" v-if="stopBtn && editor" :title="$t('head.stop')">
							<i class="icon-pause"></i><span class="hide">{{ $t('head.stop') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn btn-m" @click="control(`stop`)" v-if="stopBtn && editor" :title="$t('head.stop')">
							<i class="icon-pause"></i><span class="hide">{{ $t('head.stop') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-m"
							@click="control(`stopForce`)"
							v-if="stopForceBtn && editor"
							:title="$t('head.stop.force')"
						>
							<i class="icon-x"></i><span class="hide">{{ $t('head.stop.force') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn"
							@click="control(`interrupt`)"
							v-if="interruptBtn && editor"
							:title="$t('head.interrupt')"
						>
							<span class="hide">{{ $t('head.interrupt') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn" @click="control(`block`)" v-if="blockBtn && editor" :title="$t('head.block')">
							<span class="hide">{{ $t('head.block') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-m"
							@click="control(`unblock`)"
							v-if="unblockBtn && editor"
							:title="$t('head.unblock')"
						>
							<span class="hide">{{ $t('head.unblock') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn" @click="removeInfo" v-if="deleteBtn && editor" :title="$t('head.delete')">
							<i class="icon-delete"></i>{{ $t('head.delete') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="goModifyPanel" v-if="updateBtn && editor" :title="$t('head.update')">
							<i class="icon-edit"></i>{{ $t('head.update') }}
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-primary"
							@click="goAddInfo"
							v-if="insertBtn && editor"
							:title="`${$t('head.insert')} (${$t('head.copy')})`"
						>
							<i class="icon-plus"></i>{{ `${$t('head.insert')} (${$t('head.copy')})` }}
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-primary"
							@click="restoreMeta"
							v-if="restoreBtn && editor"
							:title="$t('head.restore')"
						>
							<i class="icon-edit"></i>{{ $t('head.restore') }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'add' === panelMode || 'clone' === panelMode">
						<a href="javascript:void(0);" class="btn btn-primary" @click="saveInfo" v-if="insertBtn && editor" :title="$t('head.insert')">
							<i class="icon-plus"></i>{{ $t('head.insert') }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'modify' === panelMode">
						<a
							href="javascript:void(0);"
							class="btn btn-primary"
							@click="updateInfo"
							v-if="updateBtn && editor"
							:title="$t('head.update')"
						>
							<i class="icon-edit"></i>{{ $t('head.update') }}
						</a>
					</div>
				</footer>
			</div>
		</div>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import merge from 'lodash/merge';
import { DetailToggleEventBus, parseFlattenObj, HttpReq, getHttpUrl } from '@/utils/common.js';
import DetailBasic from '@/components/detail/DetailBasic';
import DetailProperty from '@/components/detail/DetailProperty';
import DetailResult from '@/components/detail/DetailResult';
import DetailGrid from '@/components/detail/DetailGrid';
import DetailCustom from '@/components/detail/DetailCustom';

export default {
	name: 'Detail',
	props: {
		mode: {
			type: String,
			default: 'detail',
		},
		pk: Array,
		buttonList: Array,
		tabList: Array,
		info: Object,
		cudUrl: String,
		controlUrl: String,
		controlParams: Function,
		viewer: Boolean,
		editor: Boolean,
	},
	computed: {
		toShowResult() {
			return (this.insertBtn || this.updateBtn || this.deleteBtn || this.dumpBtn || this.restoreBtn) && this.editor;
		},
	},
	components: {
		DetailBasic,
		DetailProperty,
		DetailGrid,
		DetailCustom,
		DetailResult,
	},
	data() {
		return {
			panelMode: this.mode,
			selectedIdx: 0,
			subBarTitle: '',

			tabAllData: {},

			insertBtn: false,
			updateBtn: false,
			deleteBtn: false,
			restoreBtn: false,
			excelExportBtn: false,
			dumpBtn: false,
			loadBtn: false,
			executeBtn: false,
			guideBtn: false,

			startBtn: false,
			stopBtn: false,
			interruptBtn: false,
			stopForceBtn: false,
			blockBtn: false,
			unblockBtn: false,
			reorderBtn: false,

			httpReq: null,
			httpReqResult: {},
			showResultTab: false,

			detailData: {},

			cloneInfo: {},
		};
	},
	watch: {
		cudUrl: {
			immediate: true,
			handler() {
				this.httpReq = new HttpReq(this.cudUrl);
			},
		},
		buttonList: {
			immediate: true,
			handler() {
				this.setButtonList();
			},
		},
		mode() {
			this.panelMode = this.mode;
		},
		panelMode() {
			this.$nextTick(() => {
				//watch cloneInfo
				this.getTargetObj();
			});
		},
		info() {
			if (!this.info) return;

			this.cloneInfo = parseFlattenObj(this.info);

			if (this.info.isNewTab) {
				this.expandCollapsePanel();
				delete this.cloneInfo.isNewTab;
			}
		},
	},
	methods: {
		async getTargetObj() {
			if (null === this.panelMode) {
				return;
			}

			if ('done' !== this.panelMode) {
				this.detailData = {};

				if ('add' !== this.panelMode) {
					const param = {};

					for (const key in this.cloneInfo) {
						if (-1 === this.pk.indexOf(key)) continue;
						param[key] = this.cloneInfo[key];
					}

					const result = await this.httpReq.read(param);

					if (`ok` !== result.data.result) return;

					this.detailData = parseFlattenObj(result.data.object);

					// if ('clone' === this.panelMode) {
					// 	this.pk.forEach(pkInfo => {
					// 		this.detailData[pkInfo] = null;
					// 	});
					// }
				}
			}

			this.setButtonList();

			this.openPanel();
		},
		changeTab(idx) {
			this.selectedIdx = idx;
		},
		setButtonList() {
			this.buttonList.forEach(info => {
				this[`${info.id}Btn`] = 'function' === typeof info.isUse ? info.isUse(this.detailData) : info.isUse;
			});
		},
		openPanel() {
			const subBarTitleMsg = {
				detail: 'Detail',
				done: 'Detail',
				add: 'Insert',
				clone: 'Insert',
				modify: 'Update',
			};

			this.subBarTitle = subBarTitleMsg[this.panelMode];

			if ('done' !== this.panelMode && this.showResultTab) {
				this.selectedIdx = 0;
				this.showResultTab = false;
			}

			if (document.body.classList.contains('fixed')) {
				document.body.classList.remove(
					'panel-open-detail',
					'panel-open-add',
					'panel-open-modify',
					'panel-open-done',
					'panel-open-clone',
					'fixed',
				);

				this.$refs.panel.style.display = 'none';
			}

			document.body.classList.add('fixed');

			this.$refs.panel.style.display = 'block';

			setTimeout(() => {
				document.body.classList.add(`panel-open-${this.panelMode}`);
				DetailToggleEventBus.$emit('MainResize');
			}, 200);
		},
		closePanel() {
			document.body.classList.remove(`panel-open-${this.panelMode}`);

			setTimeout(() => {
				this.$refs.panel.style.display = 'none';
				document.body.classList.remove('fixed');

				this.$refs.panel.removeAttribute('style');

				DetailToggleEventBus.$emit('MainResize');
			}, 200);
		},
		expandCollapsePanel(evt) {
			this.$refs.panel.classList.toggle('expand');
			DetailToggleEventBus.$emit('MainResize');

			document.body.dispatchEvent(
				new CustomEvent('resize', {
					detail: {
						evt,
					},
				}),
			);
		},
		goPreviousPanel() {
			this.panelMode = 'detail';
		},
		goAddInfo() {
			this.panelMode = 'clone';
		},
		goModifyPanel() {
			this.panelMode = 'modify';
		},
		removeInfo() {
			this.$confirm({
				type: 'warn',
				message: this.$t('head.delete.conform'),
				callback: () => {
					this.loadData(async () => {
						this.pk.forEach(pkInfo => {
							if ('undefined' === typeof this.tabAllData[pkInfo]) {
								this.tabAllData[pkInfo] = this.cloneInfo[pkInfo];
							}
						});

						const param = {};

						for (const key in this.tabAllData) {
							if (-1 === this.pk.indexOf(key)) continue;
							param[key] = this.tabAllData[key];
						}

						const result = await this.httpReq.delete(param);
						this.setData(result, 'head.delete.notice', this.closePanel);
					});
				},
			});
		},
		saveInfo() {
			this.loadData(async () => {
				this.pk.forEach(pkInfo => {
					if ('undefined' === typeof this.tabAllData[pkInfo]) {
						this.tabAllData[pkInfo] = '';
					}
				});

				const result = await this.httpReq.create(this.tabAllData);
				this.setData(result, 'head.insert.notice');
			});
		},
		updateInfo() {
			this.loadData(async () => {
				this.pk.forEach(pkInfo => {
					if ('undefined' === typeof this.tabAllData[pkInfo]) {
						this.tabAllData[pkInfo] = this.cloneInfo[pkInfo];
					}
				});

				const result = await this.httpReq.update(this.tabAllData);
				this.setData(result, 'head.update.notice');
			});
		},
		async loadData(callBack) {
			this.$refs.component.forEach((comp, idx) => {
				if ('custom' !== this.tabList[idx].type && comp.getData) {
					this.tabAllData = merge(this.tabAllData, comp.getData());
				}
			});

			if (-1 === this.tabList.findIndex(tabInfo => 'custom' === tabInfo.type)) {
				if (callBack) callBack();
				return;
			}

			//get only custom data
			this.$startSpinner();

			const promiseAllList = [];

			this.$refs.component.forEach((comp, idx) => {
				if ('custom' !== this.tabList[idx].type) return;

				promiseAllList.push(
					new Promise(resolve => {
						const elementDiv = comp.$el.querySelector('[data-ready]');

						const customDataCallBack = e => {
							this.tabAllData = merge(this.tabAllData, e.detail);
							elementDiv.removeEventListener('setCustomData', customDataCallBack);
							resolve(idx);
						};

						elementDiv.addEventListener('setCustomData', customDataCallBack);

						comp.getData();
					}),
				);
			});

			try {
				await Promise.all(promiseAllList);
				if (callBack) callBack();
			} catch (error) {
				this.$alert({ type: 'warn', message: this.$t('dashboard.noMatch.criteria') });
			} finally {
				this.$stopSpinner();
			}
		},
		async setData(result, message, successCallback) {
			const resultState = 'ok' === result.data.result;

			this.httpReqResult = {
				state: resultState,
				content: resultState ? result.data.response : result.data.error,
			};

			this.selectedIdx = this.tabList.length;
			this.showResultTab = true;

			if (!resultState) {
				this.tabAllData = {};
				return;
			}

			let info = null;

			if (result.data.object) {
				info = result;
			} else {
				const param = {};

				for (const key in this.tabAllData) {
					if (-1 === this.pk.indexOf(key)) continue;
					param[key] = this.tabAllData[key];
				}

				if (0 === Object.keys(param).length) {
					for (const key in this.detailData) {
						if (-1 === this.pk.indexOf(key)) continue;
						param[key] = this.detailData[key];
					}
				}

				info = await this.httpReq.read(param);
			}

			this.tabAllData = {};

			if (`ok` === info.data.result) {
				this.detailData = parseFlattenObj(cloneDeep(info.data.object));

				if ('add' === this.panelMode || 'clone' === this.panelMode) {
					this.cloneInfo = parseFlattenObj(cloneDeep(info.data.object));
				}
			}

			this.panelMode = 'done';

			if (message) this.$alert({ type: 'normal', message: this.$t(message) });

			this.$emit('search');

			if (successCallback) successCallback();
		},
		async excelExport() {
			const exportBtnInfo = this.buttonList.filter(btn => 'excelExport' === btn.id)[0];

			if (!exportBtnInfo) return;

			this.$startSpinner();

			const req = new XMLHttpRequest();
			req.open('POST', getHttpUrl(exportBtnInfo.exportUrl), true);

			req.responseType = 'blob';

			const excelForm = document.createElement('form');

			for (const key of this.pk) {
				const hiddenField = document.createElement('input');

				hiddenField.setAttribute('type', 'hidden');
				hiddenField.setAttribute('name', key);
				hiddenField.setAttribute('value', this.cloneInfo[key]);

				excelForm.appendChild(hiddenField);
			}
			req.send(new FormData(excelForm));

			req.onload = event => {
				this.$stopSpinner();

				const blob = req.response;
				const file_name = exportBtnInfo.fileName ? `${exportBtnInfo.fileName}_${Date.now()}.xlsx` : `ExcelExport_${Date.now()}.xlsx`;

				if (blob.size <= 0 || event.target.status != '200') {
					this.$alert({ type: 'warn', message: this.$t(`head.fail.notice`) });
					return;
				}

				if (window.navigator && window.navigator.msSaveOrOpenBlob) {
					window.navigator.msSaveOrOpenBlob(blob, file_name);
				} else {
					const link = document.createElement('a');
					link.href = window.URL.createObjectURL(blob);
					link.download = file_name;
					link.click();
					URL.revokeObjectURL(link.href);
					link.remove();
				}
			};
		},
		restoreMeta() {
			this.loadData(async () => {
				const result = await new HttpReq('/common/metaHistory/restore.json').update(this.detailData);
				this.setData(result, 'head.restore.notice');
			});
		},
		async control(mod) {
			const params = this.controlParams(this.detailData);

			this.setData(await new HttpReq(`${this.controlUrl}?command=${mod}`).update(params));
		},
		guide() {
			const guideBtnInfo = this.buttonList.filter(btn => 'guide' === btn.id)[0];

			if (!guideBtnInfo) return;

			window.open(
				getHttpUrl(guideBtnInfo.url),
				'_blank',
				'height=886, width=785,resizable=yes, toolbar=no, menubar=no, location=no, scrollbars=yes, status=no',
			);
		},
	},
};
</script>
