<template>
	<div id="panel" ref="panel" class="panel panel-bottom">
		<div class="panel-dialog">
			<div class="panel-content">
				<header id="panel-header" class="panel-header sub-bar">
					<h2 class="sub-bar-tit">Detail</h2>
					<div class="ml-auto">
						<button type="button" class="btn-icon" @click="expandCollapsePanel" :title="$t('head.expand')">
							<i class="icon-expand"></i>
						</button>
						<button type="button" class="btn-icon" @click="closePanel" :title="$t('head.close')">
							<i class="icon-close"></i>
						</button>
					</div>
				</header>
				<ul class="nav nav-tabs flex-shrink-0">
					<li class="nav-item">
						<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 0 === selectedIdx }" @click="changeTab(0)">
							{{ $t(`head.make`) }}
						</a>
					</li>
					<li class="nav-item">
						<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 1 === selectedIdx }" @click="changeTab(1)">
							{{ $t(`head.import`) }}
						</a>
					</li>
				</ul>
				<!-- tab-pane -->
				<div class="panel-body tab-content">
					<div :class="{ detail: true, 'tab-pane': true, active: 0 === selectedIdx }">
						<DetailMake :detailData="detailData" :viewer="viewer" :editor="editor" />
					</div>
					<div :class="{ detail: true, 'tab-pane': true, active: 1 === selectedIdx }">
						<DetailImport :detailData="detailData" :viewer="viewer" :editor="editor" />
					</div>
				</div>

				<footer id="panel-footer" class="panel-footer sub-bar"></footer>
			</div>
		</div>
	</div>
</template>

<script>
import { DetailToggleEventBus } from '@/utils/common.js';
import DetailMake from '@/views/detail/migration/mapping/DetailMake.vue';
import DetailImport from '@/views/detail/migration/mapping/DetailImport.vue';

export default {
	name: `MigrationDetail`,
	props: {
		mod: String,
		detailData: Object,
		viewer: Boolean,
		editor: Boolean,
	},
	components: {
		DetailMake,
		DetailImport,
	},
	data() {
		return {
			selectedIdx: 0,
		};
	},
	watch: {
		mod() {
			this.openPanel();
		},
	},
	methods: {
		changeTab(idx) {
			this.selectedIdx = idx;
		},
		openPanel() {
			this.selectedIdx = `make` === this.mod ? 0 : 1;

			if (document.body.classList.contains('fixed')) {
				document.body.classList.remove(
					'panel-open-detail',
					'panel-open-add',
					'panel-open-modify',
					'panel-open-done',
					'panel-open-clone',
					'fixed',
				);

				this.$refs.panel.style.display = 'none';
			}

			document.body.classList.add('fixed');

			this.$refs.panel.style.display = 'block';

			setTimeout(() => {
				document.body.classList.add(`panel-open-detail`);
				DetailToggleEventBus.$emit('MainResize');
			}, 200);
		},
		expandCollapsePanel(evt) {
			this.$refs.panel.classList.toggle('expand');
			DetailToggleEventBus.$emit('MainResize');

			document.body.dispatchEvent(
				new CustomEvent('resize', {
					detail: {
						evt,
					},
				}),
			);
		},
		closePanel() {
			document.body.classList.remove(`panel-open-${this.panelMode}`);

			setTimeout(() => {
				this.$refs.panel.style.display = 'none';
				document.body.classList.remove('fixed');

				this.$refs.panel.removeAttribute('style');

				DetailToggleEventBus.$emit('MainResize');
			}, 200);
		},
	},
};
</script>
