<script>
import Basic from '@/components/main/Basic.vue';
import { getPrivilege, HttpReq, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'Interface',
	extends: Basic,
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	async mounted() {
		let result = await new HttpReq(`/common/property/properties.json`).read({ propertyId: 'List.Interface.InterfaceType', orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.interfaceTypeList = result.data.object;

		result = await new HttpReq(`/common/property/properties.json`).read({ propertyId: 'List.Interface.ScheduleType', orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.scheduleTypeList = result.data.object;

		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			interfaceTypeList: [],
			scheduleTypeList: [],

			cudUrl: '/igate/interface/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'interfaceId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
				},
				{
					type: 'text',
					vModel: 'interfaceName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name',
				},
				{
					type: 'modal',
					vModel: 'adapterId',
					label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.adapter'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'adapterId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId',
								},
								{
									type: 'text',
									vModel: 'adapterName',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name',
								},
								{
									type: 'text',
									vModel: 'adapterDesc',
									label: this.$t('head.description'),
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc',
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									placeholder: this.$t('head.searchCoditionList'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option',
									},
								},
							],
						},
						button: {
							list: [{ id: 'initialize', isUse: true }],
						},
						grid: {
							url: '/igate/adapter/searchPopup.json',
							paging: {
								isUse: true,
								side: 'server',
							},
							options: {
								columns: [
									{
										header: this.$t('head.id'),
										name: 'adapterId',
									},
									{
										header: this.$t('head.name'),
										name: 'adapterName',
									},
									{
										header: this.$t('head.description'),
										name: 'adapterDesc',
									},
									{
										header: this.$t('igate.adapter.structure.request'),
										name: 'requestStructure',
									},
									{
										header: this.$t('igate.adapter.structure.response'),
										name: 'responseStructure',
									},
								],
							},
						},
						rowClickedCallback: rowInfo => rowInfo.adapterId,
					},
				},
				{
					type: 'select',
					vModel: 'interfaceType',
					label: this.$t('common.type'),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Interface.InterfaceType',
							orderByKey: true,
						},
						optionListName: 'interfaceTypeList',
						optionFor: 'option in interfaceTypeList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue',
					},
				},
				{
					type: 'select',
					vModel: 'scheduleType',
					label: `${this.$t(`head.schedule`)} ${this.$t('common.type')}`,
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Interface.ScheduleType',
							orderByKey: true,
						},
						optionListName: 'scheduleTypeList',
						optionFor: 'option in scheduleTypeList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue',
					},
				},
				{
					type: 'select',
					vModel: 'disabledYn',
					label: this.$t(`igate.interface.disabledYn`),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Yn',
							orderByKey: true,
						},
						optionListName: 'useYnList',
						optionFor: 'option in useYnList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue',
					},
				},
				{
					type: 'select',
					vModel: 'usedYn',
					label: this.$t(`igate.interface.transaction.history`),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Yn',
							orderByKey: true,
						},
						optionListName: 'useYnList',
						optionFor: 'option in useYnList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue',
					},
				},
				{
					type: 'text',
					vModel: 'interfaceGroup',
					label: this.$t('head.group'),
					placeholder: this.$t('head.searchData'),
				},
				{
					type: 'dataList',
					vModel: 'privilegeId',
					label: this.$t(`common.privilege`),
					placeholder: this.$t('head.searchData'),
					regExpType: 'searchId',
					optionInfo: {
						url: '/common/privilege/list.json',
						params: {
							privilegeType: `b`,
						},
						optionListName: 'privilegeTypeList',
						optionFor: 'option in privilegeTypeList',
						optionValue: 'option.privilegeId',
						optionText: 'option.privilegeId',
					},
				},
				{
					type: 'text',
					vModel: 'interfaceDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc',
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					placeholder: this.$t('head.searchCoditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option',
					},
				},
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{
					id: 'excelExport',
					isUse: true,
					exportUrl: '/igate/interface/exportExcel.json',
					fileName: `Interface ExcelExport`,
				},
			],

			// Grid
			gridInfo: {
				url: '/igate/interface/search.json',
				totalCntUrl: '/igate/interface/rowCount.json',
				paging: {
					isUse: true,
					side: 'server',
				},
				options: {
					columns: [
						{
							header: this.$t('head.id'),
							name: 'interfaceId',
							width: '10%',
						},
						{
							header: this.$t('head.name'),
							name: 'interfaceName',
							width: '20%',
						},
						{
							header: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
							name: 'adapterId',
							width: '20%',
						},
						{
							name: 'interfaceType',
							header: this.$t('common.type'),
							width: '10%',
							align: `center`,
							formatter: ({ value }) =>
								this.interfaceTypeList.find(interfaceType => interfaceType.pk.propertyKey === value).propertyValue,
						},
						{
							name: 'scheduleType',
							header: `${this.$t(`head.schedule`)} ${this.$t('common.type')}`,
							width: '10%',
							align: `center`,
							formatter: ({ value }) => this.scheduleTypeList.find(scheduleType => scheduleType.pk.propertyKey === value).propertyValue,
						},
						{
							header: this.$t('head.group'),
							name: 'interfaceGroup',
							width: '10%',
						},
						{
							name: 'privilegeId',
							header: this.$t('common.privilege'),
							width: '10%',
						},
						{
							header: this.$t('head.description'),
							name: 'interfaceDesc',
							width: '20%',
						},
					],
				},
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['interfaceId'],
			controlUrl: `/igate/interface/control.json`,
			controlParams: detailData => ({
				interfaceId: detailData.interfaceId,
			}),
			detailButtonList: [{ id: 'dump', isUse: true }],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/interface/BasicInfo',
				},
				{
					type: 'bundle',
					label: this.$t('head.inoutput.info'),
					url: '/interface/ModelRecordInfo',
				},
				{ type: 'bundle', url: '/interface/ExtendInfo', label: this.$t('head.extension.info') },
				{
					id: `useInfo`,
					type: 'basic',
					label: this.$t('head.resource.inuse.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'lockUserId',
									label: this.$t('head.lock.userId'),
									readonly: true,
									val: '0',
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: this.$t('head.updateVersion'),
									readonly: true,
									val: '0',
								},
							],
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('head.update.userId'),
									readonly: true,
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('head.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value),
								},
							],
						],
					],
				},
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
		};
	},
};
</script>
