<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/adapter/Detail.vue';
import { getPrivilege, HttpReq, escapeHtml } from '@/utils/common.js';

export default {
	name: 'Adapter',
	extends: Basic,
	components: {
		Detail,
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(async () => {
				const result = await new HttpReq(`/common/property/properties.json`).read({ propertyId: 'List.Adapter.Type', orderByKey: true });

				if (`ok` === result.data.result) this.adapterTypes = result.data.object;

				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			adapterTypes: [],

			cudUrl: '/igate/adapter/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'adapterId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
				},
				{
					type: 'text',
					vModel: 'adapterName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name',
				},
				{
					type: 'select',
					vModel: 'adapterType',
					label: this.$t('common.type'),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Adapter.Type',
							orderByKey: true,
						},
						optionListName: 'adapterTypes',
						optionFor: 'option in adapterTypes',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue',
					},
				},
				{
					type: 'select',
					vModel: 'queueMode',
					label: `${this.$t('head.queue')} ${this.$t('head.mode')}`,
					optionInfo: {
						optionFor:
							"option in [ {text: 'File', value: 'F'}, {text: 'DB', value: 'D'}, {text: 'Memory', value: 'M'}, {text: 'Shared', value: 'S'} ]",
						optionValue: 'option.value',
						optionText: 'option.text',
					},
				},
				{
					type: 'text',
					vModel: 'adapterDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc',
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					placeholder: this.$t('head.searchCoditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option',
					},
				},
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'add', isUse: true },
			],

			// Grid
			gridInfo: {
				url: '/igate/adapter/search.json',
				totalCntUrl: '/igate/adapter/rowCount.json',
				paging: {
					isUse: true,
					side: 'server',
				},
				options: {
					columns: [
						{
							name: 'adapterId',
							header: this.$t('head.id'),
							width: `20%`,
						},
						{
							name: 'adapterName',
							header: this.$t('head.name'),
							width: `30%`,
						},
						{
							name: 'adapterType',
							header: this.$t('common.type'),
							align: `center`,
							width: `10%`,
							formatter: ({ row }) => {
								const findAdapterType = this.adapterTypes.find(info => info.pk.propertyKey === row.adapterType);
								return findAdapterType ? escapeHtml(findAdapterType.propertyValue) : '';
							},
						},
						{
							name: 'queueMode',
							header: this.$t('head.queue') + ' ' + this.$t('head.mode'),
							align: 'center',
							width: `10%`,
							formatter: ({ row }) => {
								if (`F` === row.queueMode) return `File`;
								else if (`D` === row.queueMode) return `DB`;
								else if (`M` === row.queueMode) return `Memory`;
								else if (`S` === row.queueMode) return `Shared`;
							},
						},
						{
							name: 'adapterDesc',
							header: this.$t('head.description'),
							width: `30%`,
						},
					],
				},
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['adapterId'],
			controlUrl: '/igate/adapter/control.json',
			controlParams: detailData => {
				return {
					adapterId: detailData.adapterId,
					instance: null,
				};
			},

			detailInfo: {},
			isDetailView: false,
			detailMode: null,

			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'guide', isUse: true, url: '/manual/External Guide.htm' },
				{ id: 'dump', isUse: true },
				{ id: 'start', isUse: true },
				{ id: 'stop', isUse: true },
			],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/adapter/BasicInfo',
				},
				{
					type: 'property',
					label: `${this.$t('igate.adapter')} ${this.$t('igate.operation')}`,
					dataKey: 'adapterOperations',
					addRowInfo: { type: 'common' },
					content: [
						{
							type: 'select',
							vModel: 'pk.adapterEvent',
							label: `${this.$t('igate.adapter.event')}`,
							optionInfo: {
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.Adapter.Event',
									orderByKey: true,
								},
								optionListName: 'adapterEventList',
								optionFor: 'option in adapterEventList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue',
							},
						},
						{
							type: 'modal',
							vModel: 'operationId',
							label: `${this.$t('igate.operation')} ${this.$t('head.id')}`,
							placeholder: this.$t('head.searchId'),
							modalInfo: {
								title: this.$t('igate.operation'),
								search: {
									list: [
										{
											type: 'text',
											vModel: 'operationId',
											label: this.$t('head.id'),
											placeholder: this.$t('head.searchId'),
											regExpType: 'searchId',
										},
										{
											type: 'text',
											vModel: 'operationName',
											label: this.$t('head.name'),
											placeholder: this.$t('head.searchName'),
											regExpType: 'name',
										},
										{
											type: 'select',
											vModel: 'operationType',
											label: this.$t('common.type'),
											val: 'A',
											optionInfo: {
												url: '/common/property/properties.json',
												params: {
													propertyId: 'List.Operation.OperationType',
													orderByKey: true,
												},
												optionListName: 'operationTypeList',
												optionFor: 'option in operationTypeList',
												optionValue: 'option.pk.propertyKey',
												optionText: 'option.propertyValue',
												optionIf: `option.pk.propertyKey === 'A'`,
												isViewAll: false,
											},
										},
										{
											type: 'dataList',
											vModel: 'pageSize',
											label: this.$t('head.listCount'),
											val: '10',
											optionInfo: {
												optionFor: 'option in [10, 100, 1000]',
												optionValue: 'option',
												optionText: 'option',
											},
										},
									],
								},
								button: {
									list: [{ id: 'initialize', isUse: true }],
								},
								grid: {
									url: '/igate/operation/searchPopup.json',
									totalCntUrl: '/igate/operation/rowCount.json',
									paging: {
										isUse: true,
										side: 'server',
									},
									options: {
										columns: [
											{
												header: this.$t('head.id'),
												name: 'operationId',
											},
											{
												header: this.$t('head.name'),
												name: 'operationName',
											},
										],
									},
								},
								rowClickedCallback: function (rowInfo) {
									return rowInfo.operationId;
								},
							},
						},
						{
							type: 'text',
							vModel: 'cronExpression',
							label: this.$t('igate.adapter.cronExpression'),
							regExpType: 'cron',
						},
						{
							type: 'modal',
							vModel: 'calendarId',
							label: `${this.$t('igate.calendar')} ${this.$t('head.id')}`,
							placeholder: this.$t('head.searchId'),
							modalInfo: {
								title: this.$t('igate.calendar'),
								search: {
									list: [
										{
											type: 'text',
											vModel: 'calendarId',
											label: this.$t('head.id'),
											placeholder: this.$t('head.searchId'),
											regExpType: 'id',
										},
										{
											type: 'text',
											vModel: 'calendarName',
											label: this.$t('head.name'),
											placeholder: this.$t('head.searchName'),
											regExpType: 'name',
										},
										{
											type: 'text',
											vModel: 'calendarDesc',
											label: this.$t('head.description'),
											placeholder: this.$t('head.searchComment'),
											regExpType: 'desc',
										},
										{
											type: 'dataList',
											vModel: 'pageSize',
											label: this.$t('head.listCount'),
											val: '10',
											optionInfo: {
												optionFor: 'option in [10, 100, 1000]',
												optionValue: 'option',
												optionText: 'option',
											},
										},
									],
								},
								button: {
									list: [{ id: 'initialize', isUse: true }],
								},
								grid: {
									url: '/igate/calendar/searchPopup.json',
									totalCntUrl: '/igate/calendar/rowCount.json',
									paging: {
										isUse: true,
										side: 'server',
									},
									options: {
										columns: [
											{
												header: this.$t('head.id'),
												name: 'calendarId',
											},
											{
												header: this.$t('head.name'),
												name: 'calendarName',
											},
											{
												header: this.$t('head.description'),
												name: 'calendarDesc',
											},
											{
												header: this.$t('igate.calendar.saturday'),
												name: 'saturdayYn',
											},
											{
												header: this.$t('igate.calendar.sunday'),
												name: 'sundayYn',
											},
										],
									},
								},
								rowClickedCallback: function (rowInfo) {
									return rowInfo.calendarId;
								},
							},
						},
						{
							type: 'select',
							vModel: 'disabledYn',
							label: `${this.$t('igate.adapter.disabledYn')}`,
							val: 'N',
							optionInfo: {
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.Yn',
									orderByKey: true,
								},
								optionListName: 'adapterDisabledYnList',
								optionFor: 'option in adapterDisabledYnList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue',
							},
						},
					],
				},
				{
					type: 'bundle',
					label: `${this.$t('igate.adapter')} ${this.$t('head.property')}`,
					url: '/adapter/PropertyInfo',
				},
				{
					type: 'bundle',
					label: this.$t('igate.connector'),
					url: '/adapter/ConnectorInfo',
				},
				{
					type: 'bundle',
					label: this.$t('igate.adapter.queue.deploies'),
					url: '/adapter/DeploiesInfo',
				},
			],
		};
	},
};
</script>
