<script>
import Basic from '@/components/main/Basic.vue';
import { getPrivilege, numberWithComma } from '@/utils/common.js';

export default {
	name: `ConnectorControl`,
	extends: Basic,
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			cudUrl: `/igate/connectorControl/object.json`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'select',
					vModel: 'instanceId',
					label: `${this.$t('igate.instance')} ${this.$t('head.id')}`,
					optionInfo: {
						url: '/igate/instance/list.json',
						optionListName: 'instanceList',
						optionFor: 'option in instanceList',
						optionValue: 'option.instanceId',
						optionText: 'option.instanceId',
						optionIf: "option.instanceType == 'T'",
					},
				},
				{
					type: 'select',
					vModel: 'statusInfo',
					label: `${this.$t('head.status')} ${this.$t('head.info')}`,
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Connector.StatusInfo',
							orderByKey: true,
						},
						optionListName: 'connectorStatusList',
						optionFor: 'option in connectorStatusList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue',
					},
				},
				{
					type: 'modal',
					modalInfo: {
						title: this.$t('igate.adapter'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'adapterId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId',
								},
								{
									type: 'text',
									vModel: 'adapterName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name',
								},
								{
									type: 'text',
									vModel: 'adapterDesc',
									label: this.$t('head.description'),
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc',
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option',
									},
								},
							],
						},
						button: {
							list: [{ id: 'initialize', isUse: true }],
						},
						grid: {
							url: '/igate/adapter/searchPopup.json',
							totalCntUrl: '/igate/adapter/rowCount.json',
							paging: {
								isUse: true,
								side: 'server',
							},
							options: {
								columns: [
									{
										name: 'adapterId',
										header: this.$t('head.id'),
										width: '20%',
									},
									{
										name: 'adapterName',
										header: this.$t('head.name'),
										width: '20%',
									},
									{
										name: 'adapterDesc',
										header: this.$t('head.description'),
										width: '20%',
									},
									{
										name: 'requestStructure',
										header: this.$t('igate.adapter.structure.request'),
										width: '20%',
									},
									{
										name: 'responseStructure',
										header: this.$t('igate.adapter.structure.response'),
										width: '20%',
									},
								],
							},
						},
						rowClickedCallback: rowInfo => rowInfo.adapterId,
					},
					vModel: 'adapterId',
					label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
				},
				{
					type: 'modal',
					modalInfo: {
						title: this.$t('igate.connector'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'connectorId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId',
								},
								{
									type: 'text',
									vModel: 'connectorName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name',
								},
								{
									type: 'select',
									vModel: 'connectorType',
									label: this.$t('common.type'),
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Connector.Type',
											orderByKey: true,
										},
										optionListName: 'connectorTypes',
										optionFor: 'option in connectorTypes',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue',
									},
								},
								{
									type: 'text',
									vModel: 'connectorDesc',
									label: this.$t('head.description'),
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc',
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option',
									},
								},
							],
						},
						button: {
							list: [{ id: 'initialize', isUse: true }],
						},
						grid: {
							url: '/igate/connector/searchPopup.json',
							totalCntUrl: '/igate/connector/rowCount.json',
							paging: {
								isUse: true,
								side: 'server',
							},
							options: {
								columns: [
									{
										name: 'connectorId',
										header: this.$t('head.id'),
									},
									{
										name: 'connectorName',
										header: this.$t('head.name'),
									},
									{
										name: 'connectorDesc',
										header: this.$t('head.description'),
									},
								],
							},
						},
						rowClickedCallback: rowInfo => rowInfo.connectorId,
					},
					vModel: 'connectorId',
					label: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
				},
				{
					type: 'text',
					vModel: 'connectorName',
					label: `${this.$t('igate.connector')} ${this.$t('head.name')}`,
					placeholder: this.$t('head.searchName'),
					regExpType: 'name',
				},
				{
					type: 'text',
					vModel: 'socketAddress',
					label: 'Socket Address',
					placeholder: this.$t('head.searchData'),
				},
				{
					type: 'text',
					vModel: 'socketPortSnapshot',
					label: 'Socket Port',
					placeholder: this.$t('head.searchData'),
					regExpType: 'num',
				},
				{
					type: 'text',
					vModel: 'connectorDesc',
					label: `${this.$t('igate.connector')} ${this.$t('head.description')}`,
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc',
				},
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'start', isUse: true },
				{ id: 'stop', isUse: true },
				{ id: 'stopForce', isUse: true },
				{ id: 'interrupt', isUse: true },
				{ id: 'block', isUse: false },
				{ id: 'unblock', isUse: false },
			],

			// Grid
			gridInfo: {
				url: '/igate/connectorControl/searchSnapshot.json',
				totalCntUrl: null,
				paging: {
					isUse: false,
					side: 'client',
				},
				options: {
					rowHeaders: ['checkbox'],
					header: {
						height: 60,
						complexColumns: [
							{
								name: 'sessioninfo',
								header: `${this.$t('igate.connectorControl.session')} ${this.$t('head.info')}`,
								childNames: ['sessionCount', 'sessionInuse', 'sessionMaxCount'],
							},
							{
								name: 'threadinfo',
								header: this.$t('igate.connectorControl.threadInfo'),
								childNames: ['threadCount', 'threadInuse', 'threadMax'],
							},
						],
					},
					onGridMounted: evt => {
						evt.instance.on('click', clickEvt => {
							if ('columnHeader' == clickEvt.targetType) return;

							const rows = clickEvt.instance.getCheckedRows();
							const isViewBlockBtn =
								0 === rows.length ? false : 0 < rows.filter(info => `O` !== info.requestDirection).length ? false : true;

							this.buttonList = [
								{ id: 'initialize', isUse: true },
								{ id: 'newTab', isUse: true },
								{ id: 'start', isUse: true },
								{ id: 'stop', isUse: true },
								{ id: 'stopForce', isUse: true },
								{ id: 'interrupt', isUse: true },
								{ id: 'block', isUse: isViewBlockBtn },
								{ id: 'unblock', isUse: isViewBlockBtn },
							];

							if ('processResult' !== clickEvt.columnName) return;

							if (' ' === clickEvt.instance.getFormattedValue(clickEvt.rowKey, 'processResult')) return;

							window.$alert({ type: 'normal', message: clickEvt.instance.getFormattedValue(clickEvt.rowKey, 'processResult') });
						});
					},
					columns: [
						{
							name: 'instanceId',
							header: `${this.$t('igate.instance')} ${this.$t('head.id')}`,
							align: 'left',
							width: '8%',
						},
						{
							name: 'status',
							header: `${this.$t('head.status')} ${this.$t('head.info')}`,
							align: 'center',
							width: '7%',
							formatter: name => {
								let backgroundColor = '';
								let fontColor = '#151826';

								const status = name.row.status;

								if ('Down' === status || 'Starting' === status || 'Stoping' === status) {
									backgroundColor = '';
								} else if ('Normal' === status) {
									backgroundColor = '#62d36f';
									fontColor = 'white';
								} else if ('Error' === status) {
									backgroundColor = '#ed3137';
									fontColor = 'white';
								} else if ('Fail' === status) {
									backgroundColor = '#9932a1';
									fontColor = 'white';
								} else if ('Warn' === status) {
									backgroundColor = '#b7bf22';
									fontColor = 'white';
								} else {
									backgroundColor = '#4e464f';
									fontColor = 'white';
								}

								const div = document.createElement(`div`);

								div.style.width = `100%`;
								div.style.height = `100%`;
								div.style.backgroundColor = backgroundColor;
								div.style.color = fontColor;

								div.innerText = name.row.status.toString();

								const span = document.createElement(`span`);
								span.appendChild(div);

								return span.innerHTML;
							},
						},
						{
							name: 'adapterId',
							header: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
							align: 'left',
							width: '10%',
						},
						{
							name: 'connectorId',
							header: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
							align: 'left',
							width: '10%',
						},
						{
							name: 'connectorName',
							header: `${this.$t('igate.connector')} ${this.$t('head.name')}`,
							align: 'left',
							width: '11%',
						},
						{
							name: 'socketAddress',
							header: 'Socket Address',
							align: 'left',
							width: '8%',
						},
						{
							name: 'socketPort',
							header: 'Socket Port',
							align: 'center',
							width: '6%',
						},
						{
							name: 'connectorDesc',
							header: `${this.$t('igate.connector')} ${this.$t('head.description')}`,
							align: 'left',
							width: '11%',
						},
						{
							name: 'sessionCount',
							header: this.$t('igate.connectorControl.create'),
							align: 'right',
							width: '5%',
							formatter: info => numberWithComma(info.row.sessionCount.toString()),
						},
						{
							name: 'sessionInuse',
							header: this.$t('igate.connectorControl.inuse'),
							align: 'right',
							width: '5%',
							formatter: info => numberWithComma(info.row.sessionInuse.toString()),
						},
						{
							name: 'sessionMaxCount',
							header: this.$t('igate.connectorControl.max'),
							align: 'right',
							width: '5%',
							formatter: name => {
								if (name.row.sessionMaxCount == '2147483647') return 'MAX';
								else return numberWithComma(name.row.sessionMaxCount.toString());
							},
						},
						{
							name: 'threadCount',
							header: this.$t('igate.connectorControl.create'),
							align: 'right',
							width: '5%',
							formatter: info => numberWithComma(info.row.threadCount.toString()),
						},
						{
							name: 'threadInuse',
							header: this.$t('igate.connectorControl.inuse'),
							align: 'right',
							width: '5%',
							formatter: info => numberWithComma(info.row.threadInuse.toString()),
						},
						{
							name: 'threadMax',
							header: this.$t('igate.connectorControl.max'),
							align: 'right',
							width: '5%',
							formatter: name => {
								if (name.row.threadMax == '2147483647') return 'MAX';
								else return numberWithComma(name.row.threadMax.toString());
							},
						},

						{
							name: 'processResult',
							header: this.$t('head.process.result'),
							defaultValue: ' ',
							width: '10%',
						},
					],
				},
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			controlUrl: '/igate/connectorControl/control.json',
			controlParams: ({ instanceId, connectorId }) => {
				return {
					instance: instanceId,
					connectorId: connectorId,
				};
			},
		};
	},
};
</script>
